.footer {
  padding-top: 68px;
  padding-left: 40px;
  padding-right: 70px;
  background-color: #000;
  @include r(1040) {
    padding-left: 20px;
    padding-right: 10px; }
  .row {
    @include r(995) {
      justify-content: space-between; } } }

.footer__arrows {
  @include r(768) {
    margin-bottom: 70px; } }

.footer__item {
  margin-bottom: 85px;
  @include r(768) {
    margin-bottom: 70px; } }

.footer__item-title {
  @include fontSR;
  font-size: 1.8rem;
  line-height: 200%;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 16px; }

.footer__link {
  display: block;
  @include fontFR;
  text-decoration: none;
  font-size: 1.8rem;
  line-height: 120%;
  letter-spacing: 0.12em;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 10px;
  transition: color .3s;
  &:hover {
    color: #fff; } }

